@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.shadow,div[class*="player-count"]{box-shadow:inset 0 0 10px #000000}html,body{font-family:'Libre Baskerville', serif !important;background:radial-gradient(#674352, #6D4758);height:100%;margin:0;padding:0}#app{height:100%;display:flex}.overlay{background-color:rgba(255,255,255,0.5);font-weight:bold}.token-container{height:100px;width:100px;border-radius:50%;overflow:hidden;box-shadow:0px 3px 15px rgba(0,0,0,0.2);margin-top:1em;border:solid 2px black}.token-container .token{height:100%;display:flex;justify-content:center;align-items:center;background-size:70%;background-repeat:no-repeat;background-position:center center}.token-container .token-DIAMOND{border:5px solid #fff;background-color:#fff;background-image:url(/splendor/static/media/diamond.f68b3adf.png)}.token-container .token-ONYX{border:5px solid #000;background-color:#000;background-image:url(/splendor/static/media/onyx.1a6421b0.png)}.token-container .token-RUBY{border:5px solid #F25737;background-color:#F25737;background-image:url(/splendor/static/media/ruby.1ccb5125.png)}.token-container .token-GOLD{border:5px solid #ECE42B;background-color:#ECE42B;background-image:url(/splendor/static/media/gold.6237158e.png)}.token-container .token-SAPPHIRE{border:5px solid #64C7F7;background-color:#64C7F7;background-image:url(/splendor/static/media/sapphire.5361b8d5.png)}.token-container .token-EMERALD{border:5px solid #51AE45;background-color:#51AE45;background-image:url(/splendor/static/media/emerald.84aea5eb.png)}.card-container{min-height:15vh;border:solid 1px black;border-radius:10px;overflow:hidden;background-size:100% !important;background-position:center !important}.card-container .victory-points{font-size:3em;padding-left:.25em;padding-top:.15em}.card-container[class*="ONYX"]{background:url(/splendor/static/media/onyx.91a2737b.jpg)}.card-container[class*="EMERALD"]{background:url(/splendor/static/media/emerald.9a330641.jpg)}.card-container[class*="SAPPHIRE"]{background:url(/splendor/static/media/sapphire.09712271.jpg)}.card-container[class*="RUBY"]{background:url(/splendor/static/media/ruby.e22241c4.jpg)}.card-container[class*="DIAMOND"]{background:url(/splendor/static/media/diamond.94991a91.jpg)}.card-container .cost-container .cost{border:solid 2px black;border-radius:50%;height:30px;width:30px;display:flex;justify-content:center;align-items:center}.card-container .cost-container .cost .quantity{color:white}.card-container .cost-container div[class*="_DIAMOND"] .quantity{color:black}.card-container .gem-container img{height:50px;padding:.5em}button{font-family:'Libre Baskerville', serif !important}div[class^="player-card"]{background:red;border-radius:0px 0px 30px 0px;height:150px;margin-top:1em;position:relative}div[class^="player-card"][active="true"]{border:solid 6px orange}div[class^="player-card"] .points{background:green;border-radius:50%;height:2em;display:flex;justify-content:center;width:2em;align-items:center;position:absolute;top:-.25em;right:-.25em}div[class*="player-count"]{padding-top:100%;background-color:rgba(0,0,0,0.2);border:solid 1px black;border-radius:10px;padding:.25em;position:relative;width:50%}div[class*="player-count"]:after{content:"";display:block;padding-bottom:50%}div[class*="player-count"] .card-badge{height:calc(99%)}div[class*="player-count"] .token-container{width:100%;height:100%;margin-top:0}div[class*="_ONYX"]{background-color:#000}div[class*="_RUBY"]{background-color:#F25737}div[class*="_EMERALD"]{background-color:#51AE45}div[class*="_SAPPHIRE"]{background-color:#64C7F7}div[class*="_GOLD"]{background-color:#ECE42B}div[class*="_DIAMOND"]{background-color:#fff;color:black}.noble-container{background:white;border-radius:10px;background-image:url(/splendor/static/media/catherine_de_medici.413e18c4.jpeg);background-size:cover;overflow:hidden;position:relative;background-position:center;width:50%}.noble-container:after{content:"";display:block;padding-bottom:100%}.noble-container>div{padding:.5em}.card-badge{border-radius:5px;display:flex;align-items:center;justify-content:center;margin-bottom:.25em}.card-badge.ONYX{background-color:#000;color:white}.card-badge.RUBY{background-color:#F25737}.card-badge.SAPPHIRE{background-color:#64C7F7}.card-badge.EMERALD{background-color:#51AE45}.card-badge.GOLD{background-color:#ECE42B}.card-badge.DIAMOND{background-color:#fff}

